.new-collections{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap:10px;
margin-bottom: 100px;
}
.new-collections h1{
    
    color: black;
    font-size: 50px;
    font-weight: 600;
    margin-top: 400px;
}
.new-collections    hr{
    width:800px;
    height: 6px;
    border-radius: 10px;
    background: gray;
}
.collections{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-top: 50px;
    gap: 20px;
}
